@tailwind base;
@tailwind components;
@tailwind utilities;

/* css background-image generated using https://10015.io/tools/css-background-pattern-generator */
.background {
  background-image: radial-gradient(#ffffff 2.5px, transparent 2.5px), radial-gradient(#ffffff 2.5px, transparent 2.5px);
  background-size: 36px 36px;
  background-position: 0 0, 18px 18px;
  background-color: #b1d2ff;
}


/* css loader generated using https://10015.io/tools/css-loader-generator */
.custom-loader {
   width: 50px;
   height: 50px;
   display: grid;
   border-radius: 50%;
   -webkit-mask:radial-gradient(farthest-side,#0000 40%,#000 41%);
   background:
     linear-gradient(0deg ,#766DF480 50%,#766DF4FF 0) center/4px 100%,
     linear-gradient(90deg,#766DF440 50%,#766DF4BF 0) center/100% 4px;
   background-repeat: no-repeat;
   animation: s3 1s infinite steps(12);
 }
 .custom-loader::before,
 .custom-loader::after {
    content:"";
    grid-area: 1/1;
    border-radius: 50%;
    background:inherit;
    opacity: 0.915;
    transform: rotate(30deg);
 }
 .custom-loader::after {
    opacity: 0.83;
    transform: rotate(60deg);
 }
 
 @keyframes s3 {
   100% {transform: rotate(1turn)}
 }